.input-container {
  position: relative;
  display: block;
}

.input-container:not(:last-child) {
  margin-bottom: 20px;
}

.input-item {
  width: 100%;
  border: none;
  border-bottom: 1px #e9e9e9 solid;
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  letter-spacing: .06em;
  background-color: transparent;
  transition: border cubic-bezier(0.49, 0.09, 0.44, 0.78) 0.3s;
  color: #E0B785;
}

.input-item:focus, .input-item:valid {
  border-bottom: 1px solid #E0B785;
}

.input-item:focus ~ .input-label, .input-item:valid ~ .input-label {
  font-size: 10px;
  top: -8px;
  color: #E0B785;
}

.input-label {
  position: absolute;
  transition: color cubic-bezier(0.49, 0.09, 0.44, 0.78) 0.3s;
  left: 0;
  top: 15px;
  font-size: 14px;
  transition: font-size .4s ease, top .4s ease;
  pointer-events: none;
}

.js-show {
  transform: translate(0, 0) rotate(0);
  opacity: 1;
  transition: transform 0.8s cubic-bezier(0.6, 0, 0.41, 1), opacity 0.8s cubic-bezier(0.6, 0, 0.41, 1);
}

.js-fadeIn {
  opacity: 0;
  transition: opacity 1s ease;
}

.js-slideRight {
  opacity: 0;
  transform: translate(50px, 0);
}

.js-slideLeft {
  opacity: 0;
  transform: translate(-50px, 0);
}

.js-show-onload {
  transform: translate(0, 0) rotate(0);
  opacity: 1;
  transition: transform 0.8s cubic-bezier(0.6, 0, 0.41, 1), opacity 0.8s cubic-bezier(0.6, 0, 0.41, 1);
}

@keyframes bg-animation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.svg-icon {
