}

.humburger {
  background-color: transparent;
  width: 30px;
  height: 20px;
  position: relative;
  border: none;
  padding: 10px;
  z-index: 10;
  cursor: pointer;
  display: inline-block;
  margin-right: 10px;
}

@media only screen and (min-width: 1024px), (any-hover: hover) {
  .humburger:hover .humburger-item {
    background-color: #E0B785;
  }
}

.humburger.open .humburger-item {
  background-color: #E0B785;
}

.humburger.open .humburger-item:first-child, .humburger.open .humburger-item:last-child {
  width: 0;
  transition-property: width;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger.open .humburger-item:nth-child(2) {
  transform: rotate(45deg);
  width: 80%;
  transition-delay: .3s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger.open .humburger-item:nth-child(3) {
  transform: rotate(-45deg);
  width: 80%;
  transition-delay: .3s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item {
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  border-radius: 3px;
}

.humburger-item:first-child {
  top: 0;
  left: 0;
  width: 75%;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:nth-child(2) {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:nth-child(3) {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:last-child {
  bottom: 0;
  right: 0;
  width: 75%;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

