  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  filter: grayscale(1);
  transform: rotate(25deg) scale(1.5) translate(-50%, -20%);
  z-index: 0;
}

.portfolio-card__content {
  position: absolute;
  width: 100%;
