  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78), height 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78), top 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78);
}

.portfolio-card__content .button {
  text-transform: capitalize;
  top: 10px;
  transition: top 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78), color 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78), border-color 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78);
}

